    <div class="contents_div_1">
      <PlantList  v-bind:plantList="dataPlantList" 
                        :showKeywordPlantListYn="keywordPlantListYn"
                        :plantKeyword="plantKeyword"
                  v-on:plantClicked="showPlantContent"
                  v-on:setAllPlantList="showAllPlantList">
      </PlantList>
    </div>
    <div class="contents_div_3">
      <PlantContent    class="contents_div_none"
                      v-bind:plantInfo="paramPlantInfo">
      </PlantContent>
      <PlantClassification  class="contents_div_none"
                            v-bind:plantInfo="paramPlantInfo" 
                            v-on:sendKeywordCropId="showKeywordPlantList">
      </PlantClassification>
      <PlantGuide class="contents_div_none"
                  v-bind:plantInfo="paramPlantInfo">
      </PlantGuide>
    </div>
  </div> -->
  <PlantSearch class="contents_div_full"></PlantSearch>
  <div style="clear:both;"></div>
  <PlantList  class="contents_div_full"
              v-bind:plantList="dataPlantList" 
                    :showKeywordPlantListYn="keywordPlantListYn"
                    :plantKeyword="plantKeyword"
              v-on:plantClicked="showPlantContent"
              v-on:setAllPlantList="showAllPlantList">
  </PlantList>
  <div style="clear:both;"></div>
  <PlantContent class="contents_div_full"
                  v-bind:plantInfo="paramPlantInfo">
  </PlantContent>
  <div style="clear:both;"></div>
  <PlantClassification class="contents_div_full"
                        v-bind:plantInfo="paramPlantInfo" 
                        v-on:sendKeywordCropId="showKeywordPlantList">
  </PlantClassification>
  <div style="clear:both;"></div>
  <PlantGuide class="contents_div_full"
              v-bind:plantInfo="paramPlantInfo">
  </PlantGuide>
</template>

<script>
import PlantSearch from './PlantSearch.vue';
import PlantList from './PlantList.vue';
import PlantContent from './PlantContent.vue';
import PlantClassification from './PlantClassification.vue';
import plantListJson from '../../assets/plantList.json'; // select get_result_json(17);
import PlantGuide from './PlantGuide.vue';


const dataPlantList = {};
const paramPlantInfo = {};
const dataKeywordPlantList = {};
const keywordPlantListYn = false;

const plantKeyword = '';

export default {
  props: ['plantList'], 
  components: {
    PlantSearch,
    PlantList,
    PlantContent,
    PlantClassification,
    PlantGuide,
  },
  data() {
    return {
      dataPlantList, 
      paramPlantInfo,
      dataKeywordPlantList,
      keywordPlantListYn,
      plantKeyword,
    };
  },
  created() {
    this.showAllPlantList();
  },
  mounted() {

  },
  methods: {
    showPlantContent(plantInfo) {
      this.paramPlantInfo = plantInfo;
    },
    showKeywordPlantList(plantList, plantClass) {
      // 선택한 키워드코드와 일치하는 작물 목록을 담는다.
      this.dataPlantList = {};
      this.dataKeywordPlantList = {};
      
      var dataPlantListTmp = plantListJson;
      var idx = 0;
      for(var i=0; i < plantList.length; i++) {
        for(var j=0; j < dataPlantListTmp.length; j++) {
          // console.log('plantList[i] == dataPlantList[j].plnt_id = ', plantList[i] == dataPlantListTmp[j].plnt_id);
          if (plantList[i] == dataPlantListTmp[j].plnt_id) {
            this.dataKeywordPlantList[idx] = dataPlantListTmp[j];
            idx++;
          }
        }
      }

      this.plantKeyword = plantClass.cd_nm+'('+plantClass.cd_expln+')';
      
      this.dataPlantList = this.dataKeywordPlantList;
      this.keywordPlantListYn = true;
    },
    showAllPlantList() {
      this.dataPlantList = plantListJson;
      this.keywordPlantListYn = false;
    }
    
  },
};
</script>