<template>
  <div>
    <h2>{{ this.plantInfo.plnt_nm }} 키워드</h2>
    <div v-for="(plantClass,id) in dataPlantClassification" :key="id">
      <div v-if="plantClass.plnt_id == plantInfo.plnt_id">
        <button class="keyword" v-on:click="sendKeywordCropId(plantClass)">
          #{{ plantClass.cd_nm }}({{ plantClass.cd_expln }})
        </button> 
      </div>
    </div>
  </div>    
</template>
  
  <script>
import plantClassification from '../../assets/plantClassification.json'; // select get_result_json(26);
  
const dataPlantClassification = plantClassification;

export default {
    props: {
        plantInfo: Object,
    },
    components: {
    },
    data() {
      return {
        dataPlantClassification,
      };
    },
    created() {
    },
    mounted() {
    },
    updated() {
    },
    methods: {
        sendKeywordCropId(plantClass) {
          // 선택한 키워드 관련 작물 id를 찾아서 상위 컴포넌트로 보낸다.
          var plantList = [];
          var i = 0;

          for (i; i < dataPlantClassification.length; i++) {
              if(dataPlantClassification[i].cdtyp_cd == plantClass.cdtyp_cd && dataPlantClassification[i].cd == plantClass.cd) {
                  plantList.push(dataPlantClassification[i].plnt_id)
              }
          }
          this.$emit('sendKeywordCropId', plantList, plantClass);  // plantId
        }
    },
  };
  </script>